<template>
  <div>
    <yd-cell-group>
          <yd-cell-item>
              <span slot="left">缴费金额：</span>
              <yd-input slot="right" v-model="amount" placeholder="输入缴费金额"></yd-input>
          </yd-cell-item>
          <yd-cell-item>
              <span slot="left">备　　注：</span>
              <yd-input slot="right" v-model="remark" placeholder="输入备注"></yd-input>
          </yd-cell-item>
        <p slot="bottom" style="color:#F00;padding: 0 .3rem;" ></p>
    </yd-cell-group>
    <yd-button-group>
        <yd-button size="large"　@click.native="pay">确　　定</yd-button>
    </yd-button-group>  
  </div>
</template>
<script>
  import { Loading } from 'vue-ydui/dist/lib.rem/dialog'

  export default {
    data () {
      return {
        amount: '',
        remark: ''
      }
    },

    methods: {
      pay () {
        Loading.open('正在支付，请稍后……')
        setTimeout(() => {
          Loading.close()
          this.$router.push({name: 'OrderList'})
        }, 2000)
      }
    }
  }
</script>



