import Vue from 'vue'
import Router from 'vue-router'
import OrderList from '@/components/OrderList'
import WechatBind from '@/components/WechatBind'
import OrderNew from '@/components/OrderNew'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/order/list',
      name: 'OrderList',
      component: OrderList
    },

    {
      path: '/wetchat/bind',
      alias: '/',
      name: 'WechatBind',
      component: WechatBind
    },

    {
      path: '/order/new',
      name: 'OrderNew',
      component: OrderNew
    }
  ]
})



