<template>
  <div id="app">
    <yd-layout title="xxx微信缴费">

        <router-view></router-view>

        <yd-tabbar slot="tabbar">
            <yd-tabbar-item title="我要缴费" link="/" :active="paymentActive">
                <yd-icon name="home" slot="icon"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="我的缴费记录" link="/order/list" :active="userCenterActive">
                <yd-icon name="ucenter-outline" slot="icon"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>

    </yd-layout>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      paymentActive: true,
      userCenterActive: false
    }
  },
  watch: {
    '$route' (to, from) {
      if (to.path === '/order/list') {
        this.$data.userCenterActive = true
        this.$data.paymentActive = false
      } else {
        this.$data.userCenterActive = false
        this.$data.paymentActive = true
      }
    }
  }
}
</script>

<style>
#app {
}
</style>



