<template>
    <div>
       <yd-cell-group>
            <yd-cell-item>
                <span slot="left">真实姓名：</span>
                <yd-input slot="right" v-model="realName" required placeholder="真实姓名"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left">学　　号：</span>
                <yd-input slot="right" v-model="studentId" required placeholder="学号"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        <yd-button-group>
            <yd-button size="large"　@click.native="bindWechat">绑　　定</yd-button>
        </yd-button-group>
    </div>
</template>
<script>
  // import router from '../router'
  export default {
    data () {
      return {
        realName: '',
        studentId: ''
      }
    },

    methods: {
      bindWechat () {
        this.$router.push({name: 'OrderNew'})
      }
    }
  }
</script>



