<template>
    <yd-cell-group >
        <yd-cell-item arrow type="a" href="#" v-for="item in list" :key="item.id">
            <span slot="left">{{item.title}}</span>
            <span slot="right">{{item.price}}</span>
        </yd-cell-item>
    </yd-cell-group>
</template>
<script>
  export default {
    data () {
      return {
        list: [
          {id: 1, title: '住宿费', price: 100.11},
          {id: 2, title: '学杂费', price: 2000.23},
          {id: 3, title: '学杂费1', price: 2000.23},
          {id: 4, title: '学杂费2', price: 2000.23},
          {id: 5, title: '学杂费3', price: 2000.23},
          {id: 6, title: '学杂费4', price: 2000.23}
        ]
      }
    }
  }

</script>



